@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);

* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

html {

  background-color: #F2EDE3;
  overflow-y: visible;
  
}
body {
  min-height: 252.2vh;
}
::-webkit-scrollbar {
  width: 18px;
}
::-webkit-scrollbar-thumb {
  background-color: gray;
}
::-webkit-scrollbar-track {
  background-color: #ecebcc;
  border:1px solid black;
  
}


h4 {
  position: absolute;
  top: 21%;
  left: 43%;
  color: white;
  font-size: 35px;
  font-family: 'News Cycle', sans-serif;
  letter-spacing: 4px;
}

.div h2 {
  text-align: left;
}
.div ,.a-events {
  font-size:15px;
  border:none;
  margin-left:-10px;
}
.div-input {
  background-color: rgba(237, 229, 71, 0.5); 
  -webkit-backdrop-filter: blur(10px); 
          backdrop-filter: blur(10px);
  background-color: #29252f;
background-image: url("data:image/svg+xml,%3Csvg width='12' height='24' viewBox='0 0 12 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23e9f23e' fill-opacity='0.4'%3E%3Cpath d='M2 0h2v12H2V0zm1 20c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM9 8c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zm-1 4h2v12H8V12z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
  border:1px solid white;
  width: 350px;
  position: absolute;
  left: 76%;
  top: 2.4%;
  padding: 45px;
  padding-left:70px; 
  opacity: 0.95;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.div-input:hover {
  background-color: darkslategrey;
}
div > .input {
  display:flex;
  margin-top: 15px;
  padding: 8px 20px;
  margin-left: 3px;
  border-radius: 5px;
  outline: none;
  font-size: 15px;
}

.login {
  padding:10px;
  font-family:sans-serif;
  margin-right:10px;
  font-size:28px;
  padding: 7px 25px;
  box-shadow: -1px 1px 5px #e0d7e0;
}
.register {
  padding:10px;
  font-family:sans-serif;
  font-size:28px;
  padding: 7px 25px;
  box-shadow: -1px 1px 5px #e0d7e0;
}
marquee {
  letter-spacing: 3.5px;
  font-weight: 500;
}





